/* Reset and base */

body, html, #root, .App {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background: #fafafa;
  color: #222;
  scroll-behavior: smooth;
}

/* Navbar styling is in NavBar.css as before */

/* Hero Section */

.hero-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4rem 6rem;
  background: white;
  min-height: 70vh;
  gap: 2rem;
  flex-wrap: wrap;
}

.hero-text h1 {
  font-size: 3.5rem;
  font-weight: 700;
  margin: 0 0 1rem 0;
}

.subtitle {
  font-size: 1.5rem;
  color: #555;
  margin: 0;
}

.hero-image img {
  max-width: 600px;
  max-height: 400px;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  width: 100%;
  height: auto;
}

/* Sections styling */

section {
  padding: 4rem 6rem;
  max-width: 1200px;
  margin: 0 auto;
}

h2 {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 2rem;
  color: #222;
}

/* Responsive for smaller screens */

@media (max-width: 1000px) {
  .hero-section {
    flex-direction: column;
    padding: 3rem 2rem;
  }

  .hero-text h1 {
    font-size: 2.5rem;
  }

  .hero-image img {
    max-width: 100%;
  }

  section {
    padding: 2rem;
  }
}
